/* eslint-disable */
export default function (dataObj, i18n) {
  const ATTR_NAME = 'data-translate';
  let signals = {};

  function observe(property, signalHandler) {
    if (!signals[property]) signals[property] = [];

    signals[property].push(signalHandler);
  }

  function notify(signal) {
    if (!signals[signal] || signals[signal].length < 1) return;

    signals[signal].forEach((signalHandler) => signalHandler());
  }

  function makeReactive(obj, key) {
    let val = obj[key];

    Object.defineProperty(obj, key, {
      get() {
        return val;
      },
      set(newVal) {
        val = newVal;
        notify(key);
      }
    });
  }

  function _replaceText(node, text) {
    node.textContent = text || node.textContent;
  }

  function _replaceStyle(styles, style) {
    styles.setAttribute('href',
      styles.getAttribute('href').replace(/css\/(.*?.min.css)/g, 'css/' + style + '.min.css'));
  }

  function syncNodeText(node, observable, property) {
    _replaceText(node, i18n.t([observable.language][property]));
    observe('language', function cb() {
      _replaceText(node, i18n.t([observable.language][property]));
    });
  }

  function syncStyles(styles, observable) {
    _replaceStyle(styles, observable.styles);
    observe('styles', function cb() {
      _replaceStyle(styles, observable.styles);
    });
  }

  function parseDOM(parent, observable) {
    const texts = parent.querySelectorAll(`[${ATTR_NAME}]:not([${ATTR_NAME}=""])`);
    const styles = document.querySelector('link#skin');

    syncStyles(styles, observable);

    texts.forEach((textNode) => {
      syncNodeText(textNode, observable, textNode.attributes[`${ATTR_NAME}`].value);
    });
  }

  function observeData(obj) {
    for (let key in obj) {
      if (obj.hasOwnProperty(key)) {
        makeReactive(obj, key);
      }
    }
    // parseDOM(document.body, obj);
  }

  observeData(dataObj);

  return {
    data: dataObj,
    observe,
    notify
  };
};



