import I18n from './i18n';
import Observator from './observator';

export default {
};

const stringsService = (function () {
  const strings = {'es_ES': {'greeting': 'Hola'}, 'en_UK': {'greeting': 'Hi'}};

  return {
    getString(lang, key) {
      return strings[lang][key];
    }
  };
})();
const lang = new I18n(stringsService, 'es_ES');
const customizable = { 'language': lang.language, 'styles': 'ruralvia' };
const App = Observator(customizable);

// console.log(App.data.language);

App.observe('language', () => { lang.language = customizable.language; });

customizable.language = 'en_UK';

